import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { Size, Color } from '../../../shared';

import './label.scss';

/**
 * A label component
 */
const Label = ({ children, type, size, color }) => {
  const classes = classNames('bi bi-label', `label-${color}`, {
    'label-sm': size === 'small',
    'label-lg': size === 'large',
    'label-sans': type === 'sans',
    'label-serif': type === 'serif',
    'label-mono': type === 'mono',
  });

  return (<span className={classes}>{children}</span>);
};

Label.propTypes = {
  children: PropTypes.string.isRequired,
  type: PropTypes.oneOf(['sans', 'serif', 'mono']),
  size: Size,
  color: Color,
};

Label.defaultProps = {
  type: 'sans',
  size: 'default',
  color: 'default',
};

export default Label;
